@import "variables";
$breakpoint-phone: 480px !default;
$breakpoint-tablet: 768px !default;
$breakpoint-desktop: 1024px !default;
.cf:after {
  content: "";
  display: table;
  clear: both;
}
.wrapper{
    margin: 0px 5%;
}
.page{
    color: #2e2c2c;
    margin: 0px;
    padding: 0px;
    font-family: 'Roboto', sans-serif;
    &__menu-banner{
        background-image: url(../img/menu-bg.png);
        padding: 140px 5.7%;
        @media screen and (max-width: 960px) {
        padding: 3%;
        }
    }
    &__subscribe-box{
    background-image: url(../img/subscribe-bg.jpg);
    background-position: bottom;
    }
    &__footer{
        background-color: #1e1e1e;
    }
}

.header{
    text-align: center;
    background-color: #000;
}
.menu{
    @media screen and (max-width: 960px) {
        display: none;
        }
    min-height: 100px;
    padding: 0px;
    margin: 0px;
    list-style: none;
    &__row{
        display: inline-block;
    }
    &__link{
        padding: 10% 40px;
        color: #78858e;
        text-decoration: none;
        font-size: 16px;
        text-transform: uppercase;
        &-active{
            color: #fff;
        }
    }
    &__logo{
        position: relative;
        top: 15px;
    }
}
@media screen and (max-width: 960px) {
    .menu {
        &__row{
            display: block;
            padding: 20px 0px;
            text-align: center;
            &:nth-child(4){
                display: none;
            }
        }
        &__link{
            font-weight: bolder;
        }
        
    }
}

.slider{
    color: #fff;
    text-align: center;
    width: 100%;
    background-image: url(../img/slider-image.png);
    padding: 20% 0;
    @media screen and (max-width: 960px) {
        background-image: none;
        background-color: #f16522;
        }
    &__title{
        text-transform: uppercase;
        font-weight: bold;
        font-size: 59px;
    }
    &__explanation{
        font-size: 20px;
    }
}

.info{

    &_content{
        padding-top: 150px;
        padding-left: 70px;
        float: left;
        width: 50%;
        @media screen and (max-width: 960px) {
        width: 100%;
        text-align: center;
        padding: 40px 0px;
        float:none;
        }

        &__title{
            color: #f16522;
            font-size: 50px;
        }
        &__button{
            display: inline-block;
            text-decoration: none;
            text-transform: uppercase;
            color: #fff;
            background-color: #f16522;
            padding: 15px 20px;
            font-size: 18px;
        }
    }
    &__image{
        width: 30%;
        min-height: 540px;
        background-repeat: no-repeat;
        background-image: url(../img/chef.png);
        background-position: 50% 35%;
        padding-right: 70px;
        float: right;
        @media screen and (max-width: 960px) {
        display: none;
        float: none;
        }
    }

}

.services{
    color: #2e2c2c;
    text-align: center;
    padding: 80px 0px;
    &_text{
        &__title{
            font-size: 48px;
            color: #f16522;
            font-weight: bolder;
            text-transform: uppercase;
            margin-bottom: 30px;
        }
        &__content{
            
        }
    }
    &_items{
        list-style: none;
        padding: 0px;
        margin: 0px;
        text-align: center;
        padding-top: 40px;
        &_item{
            padding: 5%;
            width: 20%;
            display: inline-block;
            @media screen and (max-width: 960px) {
        display: block;
                width: 90%;
        }
            &__image{
                
            }
            &__title{
                padding: 30px 0px;
                font-size: 28px;
                font-weight: 600;
                text-transform: uppercase;
                color: #f16522;
            }
            &__content{
                
            }
        }
    }
}

.menu-banner{
    @media screen and (max-width: 960px) {
        padding: 5%;
        }
   background: rgba(0, 0, 0, 0.8);
    
    &_title{
        float: left;
        padding: 80px 0px 70px 80px;
        @media screen and (max-width: 960px) {
        float: none;
        text-align: center;
        padding: 0px;
        }
        &__text{
            
            color: #f16522;
            text-transform: uppercase;
            font-size: 64px;
            font-weight: 600;
            &-white{
                color: #fff;
            }
        }
    }
    &__fmenu{
        float: right;
        width: 50%;
        padding: 45px 45px 80px 0px;
        @media screen and (max-width: 960px) {
            float: none;
            padding: 0px;
            width: 100%;
        }
    }
}

.fmenu{
    
    &__title{
        color: #cb561d;
        font-size: 28px;
        text-transform: uppercase;
        @media screen and (max-width: 960px) {
        text-align: center;
        }
    }
    &_list{
        color: #fff;
        list-style: none;
        margin: 0px;
        padding: 0px;
        &_item{
            padding-top: 25px;
            &__name{
                font-size: 20px;
            }
            &__addition{
                word-wrap: break-word;
                font-size: 14px;           }
        }
    }
}

.subscribe-box{ 
    color: #fff;
    text-align: center;
    min-height: 500px;
    background-color: rgba(0, 0, 0, 0.7);
    &_text{
        padding-top: 150px;
        &__slogan{
            padding: 30px 0px;
            font-size: 36px;
            font-weight: 600;
        }
    }
    &_form{
        max-width: 100%;
        margin: 0 auto;
        width: 400px;
        &__input{
            float: left;
            margin: 0px;
            display: inline-block;
            border: none;
            height: 40px;
            padding: 0px;
            width: 56%;
            padding-left: 4%;
        }
        &__button{
            float: right;
            padding: 0px;
            width: 40%;
            color: #fff;
            height: 40px;
            border: none;
            background-color: #f16522;
            margin: 0px;
            display: inline-block;
            font-size: 18px;
            font-weight: 600;
            cursor: pointer;
        }
    }
    
}
.map{
    min-height: 400px;
    background-image: url(../img/map.png);
}

.footer{
    padding: 50px 8%;
}
.social-box{
    float: left;
    width: 30%;
    @media screen and (max-width: 960px) {
        float: none;
        width: 90%;
        }
    &__logo{
        height: 62px;
        width: 77px;
        background-image: url(../img/footer-logo.png);
        margin-bottom: 25px;
        margin-left: 50px;
        @media screen and (max-width: 960px) {
        margin: 0 auto;
        }
    }
    &_list{
        list-style: none;
        margin: 0px;
        padding: 0px;
        @media screen and (max-width: 960px) {
        padding-top: 20px;
        text-align: center;
        }
        &__item{
            display: inline-block;
            margin-right: 15px;
        }
    }
}

.contacts{
    float: right;
    width: 30%;
    color: #78858e;
    margin: 0px;
    padding: 0px;
    list-style: none;
    @media screen and (max-width: 960px) {
        padding-top: 40px;
        float: none;
        width: 100%;
        }
    &_item{
        padding-bottom: 12px;
        &__image{
            display: inline-block;
            margin-right: 10px;
        }
    }
}